/* add css module styles here (optional) */

.test {
  margin: 2em;
  padding: 0.5em;
  border: 2px solid #000;
  font-size: 2em;
  text-align: center;
}

.MuiInputBase-input.Mui-disabled {
  background-color: #E6E6E6; }

.product-classification-selection-list {
  display: flex; }
  .product-classification-selection-list .product-classification-selection-list-item {
    display: flex;
    justify-content: left;
    align-items: center;
    margin-right: 10px; }
  .product-classification-selection-list .product-classification-selection-list-item-selected {
    border: 1px solid #ced4da;
    border-radius: 4px; }
    .product-classification-selection-list .product-classification-selection-list-item-selected input {
      height: 100%; }

.searchBtn {
  width: 38px;
  height: 38px;
  border-radius: 50px;
  border: 1px solid rgba(0, 0, 0, 0.15);
  display: inline;
  justify-content: center;
  align-items: center;
  background: #fff;
  cursor: pointer; }
  .searchBtn:hover {
    background-color: #fff;
    border-color: #1268fb; }
    .searchBtn:hover svg {
      width: 19px;
      height: 19px;
      color: #1268fb; }
  .searchBtn:active, .searchBtn:focus {
    border-radius: 50px; }
  .searchBtn svg {
    width: 19px;
    height: 19px;
    color: #828282; }
  .searchBtn:disabled {
    background: #f6f7fb;
    cursor: pointer; }
    .searchBtn:disabled:hover {
      background-color: #f6f7fb;
      border-color: rgba(0, 0, 0, 0.15); }
      .searchBtn:disabled:hover svg {
        color: #828282; }

.searchBtnLabel {
  width: 90px;
  height: 38px;
  border-radius: 4px;
  border: 1px solid #0062ff;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0062ff;
  color: #fff;
  font-weight: bold;
  cursor: pointer; }
  .searchBtnLabel:hover {
    color: #e9e9e9; }
    .searchBtnLabel:hover svg {
      color: #e9e9e9; }
  .searchBtnLabel svg {
    width: 23px;
    height: 23px;
    color: #fff;
    margin-right: 8px; }
  .searchBtnLabel > span {
    display: flex;
    align-items: center; }

.mdid-search-box-modal {
  width: 100%; }
  .mdid-search-box-modal .mdid-search-box-modal-title {
    padding: 10px 5px;
    border-bottom: 1px solid #e7e7e7;
    margin-bottom: 10px; }
    .mdid-search-box-modal .mdid-search-box-modal-title .mdid-search-box-modal-title-inner {
      width: 100%;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding-left: 20px; }
      .mdid-search-box-modal .mdid-search-box-modal-title .mdid-search-box-modal-title-inner > svg {
        cursor: pointer;
        color: #000;
        position: absolute;
        right: 20px; }
  .mdid-search-box-modal .mdid-search-box-modal-content .mdid-search-box-modal-content-inner {
    display: flex;
    margin-bottom: 20px; }
    .mdid-search-box-modal .mdid-search-box-modal-content .mdid-search-box-modal-content-inner > div {
      margin-right: 8px; }
  .mdid-search-box-modal .mdid-search-box-modal-actions .mdid-search-box-modal-actions-inner {
    display: flex;
    width: 100%;
    justify-content: flex-end;
    padding-right: 14px;
    margin-bottom: 15px; }
    .mdid-search-box-modal .mdid-search-box-modal-actions .mdid-search-box-modal-actions-inner > div {
      margin-right: 8px; }
  .mdid-search-box-modal .MuiDialog-paperWidthSm {
    max-width: 1500px; }

.mdid-search-box {
  display: flex; }
  .mdid-search-box > div {
    margin-right: 8px; }
